*
{
    margin:0;
    padding:0;
}
nav{
    
    background-color: rgb(136, 166, 186);
    color:white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}
nav ul{

    display:flex;
    justify-content: space-between;
    width: 40%;
    align-items: center;
}
nav ul a{
    font-size:20px;
    font-weight:300;
    color:white;
    text-decoration:none;
}